import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import './panels/Styles.css'

import Advertising from './panels/Advertising';
import Result from './panels/Result';

import Start from './panels/Start';
import Home from './panels/Home';

const App = () => {
	const [activePanel, setActivePanel] = useState('start');
	const [bDate, setBDate] = useState('');

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<View className={activePanel} activePanel={activePanel}>
			<Start id='start' go={go} />
			<Home id='home' bDate={bDate} setBDate={setBDate} go={go} />
			<Advertising id='advertising' setActivePanel={setActivePanel} go={go} />
			<Result id='result' bDate={bDate} go={go} />
		</View>
	);
}

export default App;

