import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { platform, IOS, Group, Div, InfoRow, Progress, FixedLayout, PromoBanner, Spinner } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import bridge from '@vkontakte/vk-bridge';

const osName = platform();

const Advertising = ({ id, go, setActivePanel }) => {
	const [progress, setProgress] = useState(0);
	const [promoBannerProps, setPromoBannerProps] = useState(null);
	const phrases = {
		'0': 'Пожалуйста подождите...',
		'15': 'Сверяем расположение звезд...',
		'30': 'Вычисляем закономерности...',
		'45': 'Производим расчеты по дате...',
		'60': 'Формируем результат...',
		'75': 'Еще секундочку...',
		'90': 'Готово!',
		'105': 'Готово!'
	};

	useEffect(() => {
		async function fetchData() {
			await bridge.send("VKWebAppInit");
			const { data } = await bridge.send('VKWebAppGetAds');
			setPromoBannerProps(data);
		}
		fetchData();

		const interval = setInterval(() => {
			if (progress < 100) {
				setProgress(progress => progress + 15);
			} else {
				setActivePanel('result');
			}
		}, 1000);

		return () => clearInterval(interval);
	}, [progress]);

	return (
		<Panel centered={true} separator={false} id={id}>
			<PanelHeader
				left={<PanelHeaderButton onClick={go} data-to="home">
					{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
				</PanelHeaderButton>}
			>
			</PanelHeader>
			<Group>
				<Div>
					<Spinner size="large" style={{ marginBottom: 20 }} />

					<InfoRow header={phrases[progress]}>
						<Progress value={progress} />
					</InfoRow>
				</Div>
			</Group>
			{ promoBannerProps && <FixedLayout vertical="bottom">
				<PromoBanner bannerData={promoBannerProps} />
			</FixedLayout> }
		</Panel>
	)
};

Advertising.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	setActivePanel: PropTypes.func.isRequired
};

export default Advertising;
