import React from 'react';
import PropTypes from 'prop-types';
import { 
	Panel,
	Button, 
	Div,
	FormLayout,
	Input
} from '@vkontakte/vkui';

const Home = ({ id, go, bDate, setBDate }) => {

	const onChange = (e) => setBDate(e.target.value)

	return (
		<Panel id={id} centered={true} separator={false}>
			<FormLayout className="w-100">
				<Input
					type="date"
					top="Дата рождения"
					value={bDate}
					onChange={onChange}
					/>
			</FormLayout>
			<Div>
				<Button disabled={!bDate} size="xl" onClick={go} data-to="advertising">Получить расчет</Button>
			</Div>
		</Panel>
	)
};

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	setBDate: PropTypes.func.isRequired,
	bDate: PropTypes.string.isRequired
};

export default Home;
