		let sum1 = nums[0] + nums[1] + nums[2] + nums[3];

		// 2. Складываем все цифры года из нашей даты:
		let sum2 = nums[4] + nums[5] + nums[6] + nums[7];

		// 3. Складываем полученные числа:
		// (1-е рабочее число)
		let sum3 = (sum1 + sum2) < 10 ? '0' + (sum1 + sum2) : '' + (sum1 + sum2);

		// 4. Цифры из первого рабочего числа складываем между собой:
		//  (2-е рабочее число)
		let arr_for_sum4 = sum3.split('');
		let sum4 = 0;

		for (let i in arr_for_sum4) {
			sum4 = sum4 + arr_for_sum4[i]*1
		}

		sum4 = sum4 < 10 ? '0' + sum4 : '' + sum4;

