		// (3-е рабочее число)
		let sum5 = (sum3 - 2*nums[0]) < 10 ? '0' + (sum3 - 2*nums[0]) : '' + (sum3 - 2*nums[0]);
		
		// 6. Ищем сумму цифр из третьего рабочего числа:	
		// (4-е рабочее число)
		let arr_for_sum6 = sum5;
		let sum6 = 0;
		
		for (let i in arr_for_sum6) {
			sum6 = sum6 + arr_for_sum6[i]*1
		}

		sum6 = sum6 < 10 ? '0' + sum6 : '' + sum6;

		let result_nums = (date + sum3 + sum4 + sum5 + sum6).split('');

		for (let i in result_nums) {
			tmp_result[result_nums[i]] = tmp_result[result_nums[i]] + result_nums[i];
		}

		setResult(result => tmp_result);
